import styles from './Button.module.scss';

interface ContainedButtonProps {
  onClick?: () => void;
  isClickable?: boolean;
  classNames?: string;
  children: React.ReactNode;

  isSelected?: boolean;
  isHidden?: boolean;
}

const ContainedButton = ({
  onClick,
  isClickable,
  classNames,
  children,
  isHidden,
}: ContainedButtonProps) => {
  const buttonClassNames = `
      ${styles.containedButton}
      ${classNames}
      ${typeof isClickable === 'undefined' || isClickable ? styles.clickable : ''}
      ${typeof isHidden === 'undefined' || !isHidden ? '' : styles.hidden}
    `;
  return (
    <button className={buttonClassNames} onClick={onClick}>
      {children}
    </button>
  );
};

export default ContainedButton;
